@import "../../styles/global.less";

.content-section {
    width: 100%;
    min-height: 60vh;
}

.home-page-img {
    width: 100%;
    height: 100%;
    display: block;
    margin: 0 auto;
}

.services {
    padding: 50px 100px 100px 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    .services-title {
        font-size: 28px;
        font-weight: bold;
    }

    .services-summary-title {
        font-size: 18px;
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .services-summary-description {
        font-size: 18px;
        margin-bottom: 20px;
    }

    .services-item {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        flex-direction: row;

        .services-item-content {
            display: flex;
            align-items: center;
            width: fit-content;
            margin-right: 10px;
            margin-bottom: 10px;
            transition: all 0.3s ease;
            cursor: pointer;

            &:hover {
                transform: translateY(-5px);
                box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
            }

            .services-item-img {
                width: 288px;
                height: 388px;
                background-color: rgb(161, 197, 228);
                margin-right: 40px;
            }

            .services-item-description {
                width: 288px;
                height: 388px;
                background-color: rgb(78, 77, 171);
                padding: 40px 20px 10px 20px;
                color: #fff;

                display: flex;
                align-items: center;
                flex-direction: column;

                .services-item-description-title {
                    margin-bottom: 30px;
                    font-size: 20px;
                }
            }
        }
    }
}

.team {
    padding: 50px 100px 100px 100px;

    .team-title {
        font-size: 40px;
        color: @primary-color;
        margin-bottom: 80px;
    }

    .team-des {
        font-size: 20px;
        padding-left: 10px;
        border-left: rgb(78, 77, 171) 4px solid;
        margin-bottom: 100px;
    }

    .team-img {
        margin-right: 10px;
    }
}

.contact {
    padding: 40px;

    .contact-title {
        color: rgb(78, 77, 171);
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 30px;
        text-align: center;
    }

    .contact-detail {
        display: flex;
        justify-content: space-evenly;

        .contact-des {
            .contact-des-item {
                background-color: #bababa;
                width: 100px;
                height: 100px;
                margin-bottom: 50px;
            }
        }

        .contact-form {
            max-width: 800px;
            padding: 20px;
            border: #bababa 1px solid;

            .form-row {
                display: flex;
                gap: 20px;

                .ant-form-item {
                    flex: 1;
                }
            }

            .ant-form-item-label {
                font-weight: 500;
            }

            .ant-btn-primary {
                min-width: 120px;
            }
        }
    }
}

@primary-color: #1890ff;@link-color: #1890ff;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@border-radius-base: 4px;