// 全局变量
@primary-color: rgb(78, 77, 171);
@secondary-color: #fff;

// 全局样式
.layout {
    min-height: 100vh;

    .ant-layout-header {
        padding: 0 50px;
    }

    .ant-layout-content {
        // padding: 50px;

        .site-layout-content {
            padding: 24px;
            min-height: 280px;
        }
    }

    .ant-layout-footer {
        text-align: center;
    }
}

// 响应式设计
@media (max-width: 768px) {
    .layout {
        .ant-layout-header {
            padding: 0 20px;
        }

        .ant-layout-content {
            // padding: 20px;
        }
    }
}

