@import "./styles/global.less";

.header {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

// 平滑滚动
html {
    scroll-behavior: smooth;
}

@primary-color: #1890ff;@link-color: #1890ff;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@border-radius-base: 4px;